import React from 'react'
import Hammer from 'hammerjs'
import '../App.css'

class RandomScreen extends React.Component {
  componentDidMount() {
    this.node = this.refs.randomTune
    this.hammer = new Hammer(this.node)
    this.hammer.get('pan').set({ direction: Hammer.DIRECTION_ALL })
    this.hammer.on('panmove', (e) => this.onPanMove(e))
    this.hammer.on('panend', (e) => this.onPanEnd(e))
  }

  componentWillUnmount() {
    this.hammer.off('panmove', (e) => this.onPanMove(e))
    this.hammer.off('panend', (e) => this.onPanEnd(e))
  }

  onPanMove(e) {
    this.node.style.transform = `translate(${e.deltaX}px, ${e.deltaY}px)`
  }

  onPanEnd(e) {
    const { randomTune, setNewRandomTune, toggleSelected } = this.props

    console.log(e.deltaX)
    if (Math.abs(e.deltaX > 125)) {
      if (e.deltaX < -125) {
        toggleSelected(randomTune)
      }
      setNewRandomTune()
    }
    else {
      this.node.style.transform = `translate(-${e.deltaX}px, -${e.deltaY}px)`
    }
  }

  render() {
    const { randomTune, setNewRandomTune } = this.props

    if (randomTune) return (
      <div className="Random-screen">
        <div 
          ref="randomTune"
          className="Random-tune" 
          onClick={setNewRandomTune}
        >
          <span className="Tune-title">
            {randomTune.title}
          </span>
          <span className="Tune-detail">
            {randomTune.composer} ({randomTune.year})
          </span>
        </div>
      </div>
    )
    else return (
      <div className="Random-screen">
        <div className="Random-tune" onClick={setNewRandomTune}>
          <span className="Tune-detail">Loading random tune...</span>
        </div>
      </div>
    )
  }
}

export default RandomScreen


